.research-container {
  /* this class is used to give a max-width to the element it is applied to, and center it horizontally when it reaches that max-width */
  width: 90%;
  max-width: 768px;
  margin: 0 auto;
}
.research-container::after {
  /* clearfix */
  content: '';
  display: table;
  clear: both;
}

.cd-main-content {
  /* you need to assign a min-height to the main content so that the children can inherit it*/
  height: 100%;
  position: relative;
  z-index: 1;
}

.cd-fixed-bg {
	position: relative;
	min-height: 100vh;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center center;
	z-index: 1;
}
.cd-fixed-bg h1, .cd-fixed-bg h2 {
	position: absolute;
	left: 50%;
	top: 50%;
	bottom: auto;
	right: auto;
	-webkit-transform: translateX(-50%) translateY(-50%);
	-moz-transform: translateX(-50%) translateY(-50%);
	-ms-transform: translateX(-50%) translateY(-50%);
	-o-transform: translateX(-50%) translateY(-50%);
	transform: translateX(-50%) translateY(-50%);
	width: 90%;
	max-width: 1170px;
	text-align: center;
	font-size: 30px;
	font-size: 1.875rem;
	text-shadow: 0 1px 3px rgba(0, 0, 0, 0.3);
	color: white;
}
.cd-fixed-bg {
	background-position: top;

	&.cd-bg-1 {
		background-image: url("../images/research/cd-background-1.jpg");
	}
	&.cd-bg-2 {
		background-image: url("../images/research/cd-background-2.jpg");
	}
	&.cd-bg-3 {
		background-image: url("../images/research/cd-background-3.jpg");
	}
	&.cd-bg-4 {
		background-image: url("../images/research/cd-background-4.jpg");
	}
}

@media only screen and (min-width: 768px) {
	.cd-fixed-bg h1, .cd-fixed-bg h2 {
		font-size: 36px;
	}
}
@media only screen and (min-width: 1170px) {
	.cd-fixed-bg {
		background-attachment: fixed;
	}
	.cd-fixed-bg h1, .cd-fixed-bg h2 {
		font-size: 48px;
		font-weight: 300;
	}
}

.cd-scrolling-bg {
	position: relative;
	min-height: 100%;
	padding: 4em 0;
	line-height: 1.6;
	box-shadow: 0 0 50px rgba(0, 0, 0, 0.5);
	z-index: 2;
	font-weight: 400;
}
.cd-scrolling-bg.cd-color-1 {
	background-color: #9cb3a8;
	color: #243040;
}

.cd-scrolling-bg.cd-color-2 {
	background-color: #99a478;
	color: #3d3536;
}

.cd-scrolling-bg.cd-color-3 {
	background-color: #b4d7a8;
	color: #3d3536;
}

.cd-scrolling-bg.cd-color-4 {
	background-color: #93c6ae;
	color: darken(#93c6ae, 50%);
}


@media only screen and (min-width: 768px) {
	.cd-scrolling-bg {
		padding: 8em 0;
		font-size: 20px;
		font-size: 1.25rem;
		line-height: 2;
	}
}